@import url(https://fonts.googleapis.com/css?family=Comfortaa);
.card {
    background: #fff;
    border-radius: 4px;
    height: 180px;
    margin: 1rem;
    position: relative;
    width: 180px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  /* .card:hover, .card:focus{
    -moz-box-shadow:10px 10px 7px rgba(0,0,0,.7);
    -webkit-box-shadow: 10px 10px 7px rgba(0,0,0,.7);
    box-shadow:10px 10px 7px rgba(0,0,0,.7);
    -webkit-transform: scale(1.25);
    -moz-transform: scale(1.25);
    -o-transform: scale(1.25);
    position:relative;
    z-index:5;
  } */
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    
  }
  .card > .img-container > img {
    width: 100%;
    background-size: cover;
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
  
  
  
.wrapper {
    width: 900px;
    padding-right: 200px;
    padding-left: 200px;
    padding-top: 50px;
    padding-bottom: 50px;
    background: rgb(146, 146, 146);
    background-image: url(/Clicky-Game/static/media/pexels-photo-245250.1c7f8909.jpeg);
    height: 70%;
    display: flex;
    flex-wrap: wrap;
    padding: 20px;
    justify-content: center;
    overflow: auto;
    margin: 100px auto;
    z-index: 5;
    border-radius: 8px;
  }
  .image-container {
    width: 160px;
    height: 160px;
  }
  
.title {
    background-image: url(/Clicky-Game/static/media/pexels-photo-1629236.3bf48687.jpeg);
    width: 100%;
    text-align: center;
    font-size: 3rem;
    /* padding-bottom: 50px; */
    height: 80px;
    padding: 180px 20px;
    color: white;
    background-size: cover;
    border-radius: 8px;
    border: 2px solid white;
  }
.name {
    font-weight:bold;
}
nav {
    width: 100%;
    color: rgb(241, 231, 231);
    margin-bottom: 50px;
    background-color: rgb(17, 10, 10);
    height: 80px;
    /* font-family: Arial, Helvetica, sans-serif; */
    position: fixed;
    top: 0;
    z-index: 3;
    font-size: 30px;
    font-weight: 500;
  }
ul {
  display: flex; 
  flex-direction: row;
  justify-content: space-evenly;
  list-style-type: none;
}
#score, #topscore {
  float: right;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Comfortaa', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(204, 204, 204);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

